html {
  font-size: 100%;
  box-sizing: border-box;
  overflow-x: hidden;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font: var(--font);
  font-size: 1rem;
  min-height: 150vh;
  background: var(--vrydarkblue);
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

h1,
h2,
h3 {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

:root {
  --limegreen: hsl(163, 72%, 41%);
  --lightpurple: hsl(243, 78%, 82%);
  --hotpink: hsl(338, 100%, 69%);
  --offsetblue: hsl(240, 100%, 73%);
  --paleblue: hsl(225, 100%, 94%);
  --brightblue: hsl(245, 75%, 52%);
  --brightred: hsl(0, 83%, 49%);
  --vrypaleblue: hsl(225, 100%, 98%);
  --desturatedblue: hsl(224, 23%, 55%);
  --darkblue: hsl(225, 46%, 20%);
  --offwhite: hsl(225, 50%, 98%);
  --vrydarkblue: hsl(226, 43%, 10%);
  --purple: hsl(246, 80%, 60%);
  --paleblue: hsl(236, 100%, 87%);
  --lightorange: hsl(15, 100%, 70%);
  --softblue: hsl(195, 74%, 62%);
  --lightred: hsl(348, 100%, 68%);
  --green: hsl(145, 58%, 55%);
  --violet: hsl(264, 64%, 52%);
  --yellow: hsl(43, 84%, 65%);
}

:root {
  --font: font-family: "Rubik", sans-serif;
}

main {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.container {
  padding: 0 6.25rem;
  max-width: 117.5rem;
  height: 100vh;
  margin: 0 3rem;
  display: inline-flex;
  align-items: center;
}

.cards {
  padding-left: 1.875rem;
  display: grid;
  grid-template-columns: repeat(auto-fit, 15.9375rem);
  grid-template-areas: "card1 card2 card3" "card4 card5 card6";
  gap: 2.5rem;
  height: 100vh;
  align-content: center;
  align-items: center;
}
@media (min-width: rem-calc(1024)) {
  .cards {
    display: grid;
    grid-template-columns: repeat(auto-fit, 15.9375rem);
    grid-template-rows: repeat(auto-fit, 12.5rem);
    grid-template-areas: "card1 card2" "card3 card4" "card5 card6";
  }
}

.results {
  background: var(--darkblue);
  color: var(--vrypaleblue);
  width: 15.9375rem;
  height: 12.5rem;
  font: var(--font);
  border-radius: 0.625rem;
  text-align: start;
  padding: 1.875rem;
}
.results--work {
  border-top: 2.75rem solid var(--lightorange);
  border-radius: 0.9375rem;
}
.results--play {
  border-top: 2.75rem solid var(--softblue);
  border-radius: 0.9375rem;
}
.results--study {
  border-top: 2.75rem solid var(--lightred);
  border-radius: 0.9375rem;
}
.results--exercise {
  border-top: 2.75rem solid var(--green);
  border-radius: 0.9375rem;
}
.results--social {
  border-top: 2.75rem solid var(--violet);
  border-radius: 0.9375rem;
}
.results--selfcare {
  border-top: 2.75rem solid var(--yellow);
  border-radius: 0.9375rem;
}
.results__title {
  margin-bottom: 1.25rem;
  padding-left: 0.625rem;
  font-size: 1rem;
  font-weight: 600;
}
.results__time {
  font-size: 2.5rem;
  padding-left: 0.625rem;
  margin-bottom: 0.3125rem;
}
.results__date__weekly {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  align-content: end;
  height: 3vh;
  color: var(--paleblue);
  font-size: 0.9375rem;
  justify-content: center;
}
.results__date__weekly--up {
  color: var(--limegreen);
}
.results__date__weekly--down {
  color: var(--brightred);
}
.results__date__weekly--netrual {
  color: var(--paleblue);
}

.profile {
  width: 15.9375rem;
  font: var(--font);
}
.profile__header {
  height: 18.75rem;
  display: grid;
  grid-template-rows: 125px 20px 170px 1fr;
  border-radius: 0.9375rem;
  padding: 1.875rem;
  background: var(--purple);
  color: var(--paleblue);
}
.profile__header--image {
  height: 5.3125rem;
  width: 5.3125rem;
  border: 3px solid var(--offwhite);
  border-radius: 50%;
}
.profile__header--report {
  font-weight: 500;
  font-size: 1.25rem;
}
.profile__header--name {
  color: var(--offwhite);
  font-size: 2.25rem;
  margin-top: 0.625rem;
}
.profile__body {
  height: 9.375rem;
  display: grid;
  border-radius: 0.625rem;
  padding: 1.875rem;
  background: var(--darkblue);
  color: var(--paleblue);
}
.profile__body--daily a {
  color: var(--offsetblue);
  font-weight: 300;
  font-size: 1.125rem;
  text-align: center;
  text-decoration: none;
  transition: all 300ms ease-in-out;
}
.profile__body--daily a:hover {
  color: var(--offwhite);
  text-decoration: none;
}
.profile__body--weekly a {
  color: var(--offsetblue);
  font-weight: 300;
  font-size: 1.125rem;
  text-align: center;
  text-decoration: none;
  transition: all 300ms ease-in-out;
}
.profile__body--weekly a:hover {
  color: var(--offwhite);
  text-decoration: none;
}
.profile__body--monthly a {
  color: var(--offsetblue);
  font-weight: 300;
  font-size: 1.125rem;
  text-align: center;
  text-decoration: none;
  transition: all 300ms ease-in-out;
}
.profile__body--monthly a:hover {
  color: var(--offwhite);
  text-decoration: none;
}